body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul , li{
  list-style: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.post-wrapper{
  width: 100%;
}
.post-wrapper > ul{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.post{
  width: 33.33333%;
  padding: 15px;
}
.post > figure {
  width: 100%;
}
.post > figure > img{
  width: inherit;
  display: block;
}

.post-info{
  display: flex;
  flex-wrap: wrap;
}
.post-info > button{ 
  display: flex;
  flex-grow: 1;
  padding: 10px;
  justify-content: center;
  cursor: pointer;
}
.post-info > button > i{
  font-size: 18px;
  margin-right: 10px;
}
.caption{
  padding:10px 0;
  border-bottom: 1px solid #c1c1c1;
}

.fa-heart{
  color: red;
}

.single{
  width: 800px;
  margin: 100px auto;
  display: flex;
}
.single > .post{
  width: 50%;
}
.single > .post-comment{
  width: 50%;
  padding: 15px;
}
.single > .post-comment > ul{
  padding: 10px;
}
.single > .post-comment > ul > li{
  padding:10px 0px;
  border-bottom: 1px solid #333;
}
.single > .post-comment > form{
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
}
.single > .post-comment > form > input{
  width: 100%;
  margin-bottom: 10px;
  height: 35px;
  padding: 0 10px;
  border: none;
  border-bottom: 1px solid orange;
  outline: none;
}
.btn-add{
  display: none;
}